.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.35;
  height: 100%;
}

.sidebar__search {
  display: flex;
  align-items: center;
  background-color: #f6f6f6;
  height: 39px;
  padding: 10px;
}

.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: white;
  width: 100%;
  height: 35px;
  border-radius: 20px;
}

.sidebar__searchContainer>.MuiSvgIcon-root {
  color: gray;
  padding: 10px;
}

.sidebar__searchContainer>input {
  border: none;
  margin-left: 10px;
  width: 100%;
  height: 100%;
}

.sidebar__header {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border-right: 1px solid lightgray;
}

.sidebar__headerRight {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 10vw;
}

.sidebar__headerRight>.MuiSvgIcon-root {
  margin-right: 2vw;
  font-size: 24px !important;
}

.sidebar__chats {
  background-color: white;
  flex: 1;
  overflow-y: hidden;
  scroll-behavior: smooth;
}

.sidebar__chats:hover {
  overflow-y: scroll;
}

.sidebar__chats::-webkit-scrollbar {
  width: 0.4rem;
}

.sidebar__chats::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
  border-radius: 3px;
}

@media screen and (max-width:700px) {
  .sidebar {
    flex: 1;
  }
}